.header__logo {
  margin: 0;
  padding: 0;
  object-fit: contain;
  object-position: center;
  width: 142px;
  height: 33px;
}

@media screen and (max-width: 690px) {
  .header__logo {
    margin-left: 30px;
  }
}

@media screen and (max-width: 425px) {
  .header__logo {
    max-width: 103.74px;
    min-height: 24.4px;
  }
}
