.popup {
  visibility: hidden;
  margin: 0;
  padding: 0;
  z-index: 1;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgb(0, 0, 0, 0.5);
  display: flex;
  opacity: 0;
  transition: visibility 0s 0.5s, opacity 0.5s linear;
}